<template>

	<tm-fullView>
		<tm-menubars title="快速动作操作栏" color="bg-gradient-primary-lighten" :showback="true"></tm-menubars>
		<tm-sheet :shadow="24">
			<view class="pa-12 text-size-s text-weight-b blue text mb-32 round-2"
